// @flow

import styled from 'styled-components/macro';
import {lighten} from '../utilities';
import {page, spacing, colors} from '../config/styles';

export default function Button(props: {}) {
    return <StyledButton {...props} />;
}

export const StyledButton = styled.button`
    position: relative;
    appearance: none;
    line-height: 1.3;
    font-size: 0.9rem;
    font-family: ${page.fontFamily};
    padding: ${spacing.regular} ${spacing.large};
    border-radius: 2px;
    height: auto;
    ${colors.withVar('white', 'background-color')};
    border: 1px solid rgba(0, 0, 0, 0.1);
    ${colors.withVar('white', 'color')};
    cursor: pointer;

    &::before {
        content: '';
        display: block;
        transition: opacity 100ms ease-in;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        z-index: -1;
        opacity: 1;
        ${colors.withVar('primary', 'background-color')};
    }

    &:hover &::before {
        opacity: 0.9;
    }
`;
